import React, { Component } from 'react'

import InputSave from "../inputsave/InputSave"
import InputLoad from "../inputload/InputLoad"

import './copyPaste.css'

export default class CopyPaste extends Component {
    state = {
        displayToggle : true
    }
    onToggleClick = (bool, _) => {
        this.setState({
            displayToggle: bool
        })
    }

    button = (colorChoice, toggleChoice, txt) => { // "btn btn-dark border-secondary"
        let color = colorChoice ? "btn btn-dark border-warning" : "btn btn-dark border-secondary"
        let style = ` ${color}`
        return (
            <button className={style}
                onClick={this.onToggleClick.bind(this, toggleChoice)}>
                {txt}
            </button>
        )
    }

    render() {
        let {displayToggle} = this.state
        return (
            <div className="cardborder">
                <div className=" d-flex flex-row bg-dark">
                    {this.button(displayToggle, true, "Copy")}
                    {this.button(!displayToggle, false, "Paste")}
                </div>

                <div className="cardborder">
                    {displayToggle? <InputSave/>: <InputLoad/>}
                </div>
            </div>
        )
    }
}
