const uriCopy = "http://52.47.50.123:80/copy"
const uriPaste = "http://52.47.50.123:80/paste"


async function copyData(data) {
    let resp = await axios.post(uriCopy, data)
    return resp.data
}

async function pasteData(data) {
    let resp = await axios.post(uriPaste, data)
    return resp.data
}

