import React from 'react'
import Sketch from 'react-p5'
import data from './dogePoints'


let seekers = []
let seekerGoalReachedN = 0

class Seeker {
    constructor(p5, aimX, aimY) {
        this.p5 = p5

      

        this.targets = {
            top: {
                x: p5.random(0, p5.width),
                y:0,
            },
            mid: {
                x: aimX,
                y: aimY,
            },
            bottom: {
                x: p5.random(0, p5.width),
                y: p5.height,
            },
        }
        this.targetState = "mid"
        this.currentX = this.targets.top.x
        this.currentY = this.targets.top.y
        this.stopped = false
    }

    changeState(){
        if (this.targetState == "top") { this.targetState = "mid"; return }
        if (this.targetState == "mid") { this.targetState = "bottom"; return }
        if (this.targetState == "bottom") {
            this.targetState = "mid"
            this.currentX = this.targets.top.x
            this.currentY = this.targets.top.y
        }
    }


    move(halt) {
        if (halt && this.stopped) {
            return
        }

        const currentTargetX = this.targets[this.targetState].x
        const currentTargetY = this.targets[this.targetState].y

        const dx = (currentTargetX - this.currentX)
        const dy = (currentTargetY - this.currentY)

        // const deltaSpeed = 50 // 100
        this.currentX += dx / this.p5.random(40,60)
        this.currentY += dy / this.p5.random(40,60)

        // Change state when target is hit.
        const targetLeeway = this.targetState === "mid"? 0.05: 10 // 0.05
        if (this.p5.abs(dx) < targetLeeway && this.p5.abs(dy) < targetLeeway) {
            this.changeState()
            // this.p5.noLoop()
            this.stopped = true
        }
    }

    render() {
        this.p5.fill(0)
        this.p5.noStroke()
        this.p5.ellipse(this.currentX, this.currentY, 3)
    }
}


const setup = (p5, canvasParentRef) => {
    p5.createCanvas(200,200).parent(canvasParentRef)
    p5.frameRate(30);
    // Data is in data.json folder in the side bar <-
    // Adding to seeker collection in global.
    Object.keys(data).forEach(i => {
        let newSeeker = new Seeker(
            p5,
            data[i].x,
            data[i].y
        )
        seekers.push(newSeeker)
    })
}

const draw = (p5) => {
    p5.background(0.2 * 255, 0.2275 * 255, 0.2549 * 255)
    seekers.forEach(s => {
        s.render()
        s.move()
    })
}


export default function Doge ({props}) {
    return <Sketch setup={setup} draw={draw}/>
}
