import React, { Component } from 'react'
import InputGroup from "../InputGroup.jsx"

import { pasteData } from "../../api.js"

export default class InputSave extends Component {
    state = {
        input: "",
        mnemonics: ""
    }

    onSubmitForm = (e) => {
        e.preventDefault()
        pasteData(this.state.input).then(data => {
            console.log(data)
            this.setState({ mnemonics: data })
        })
        this.setState({ input: "" })
    }


    // # Input bar.
    inputGroup = () => {
        return (
            <InputGroup
                placeholder={"..."}
                value={this.state.input}
                onChange={v => {
                    this.setState({
                        input: v.target.value
                    })
                }}
            />
        )
    }

    // # Popup with mnemonics after response
    mnemonics = () => {
        if (this.state.mnemonics.length === 0) {
            return <div />
        }
        return (
            <div>
                {this.state.mnemonics}
            </div>
        )

    }

    render() {
        return (
            <div className="card mb-3 border-0">
                <div className="card-body bg-dark">
                    <div className="card-header bg-dark 
                        text-success d-flex justify-content-center">
                        Input Mnemonics!
                    </div>
                    <form className="form-group" onSubmit={this.onSubmitForm}>
                        {this.inputGroup()}
                        <input
                            type="submit"
                            className="btn btn-dark btn-block 
                                    text-success custom-btn border-secondary"
                            value="Paste"
                        />
                    </form>
                    <div className="card-footer bg-dark 
                        text-warning d-flex justify-content-center">
                        {this.mnemonics()}
                    </div>
                </div>
            </div>
        )
    }
}


